.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.background { 
   background-image: url("./img/bg.jpeg");
   max-width: 100%;
   max-height: 100%;
   text-align: center; 
   top: 0;
   left: 0;
   width: 100%;
   height: 100%;
  position: fixed;
  justify-content: center;
}

.rfoto{
  border-radius: 50%;
  width: 180px;
}

.nome{
  color: rgb(228, 228, 228);
  font-family: 'Poppins', sans-serif;
  padding-top: 15px;
}

.conteudo{
  background: rgba(0, 0, 0, 0.7);
  align-content: center;
  justify-content: center;
  margin: 10%;
  padding: 30px;
}

.botoes{
  padding-top: 15px;
  align-content: center;
  justify-content: center;
}

.botao{
  margin: 5px;
}